
import { defineComponent, PropType } from "vue";
import StarRating from "@/components/reusable/StarRating.vue";
import ButtonApp from "@/components/reusable/ButtonApp.vue";
import API from "@/api/apartment";
import { mapState } from "vuex";

export default defineComponent({
  name: "ApartmentDescription",
  components: {
    StarRating,
    ButtonApp,
  },
  data() {
    return {
      id: this.$route.params.id,
      isReserved: false,
      isLoading: false,
      // card: {
      //   imgUrl: this.imgUrl,
      //   description: this.description,
      //   rating: this.rating,
      //   price: this.price,
      //   title: this.title,
      // },
    };
  },
  props: {
    imgUrl: {
      type: String as PropType<string>,
      default: "",
    },
    description: {
      type: String as PropType<string>,
      required: true,
    },
    rating: {
      type: Number as PropType<number>,
      default: 0,
    },
    price: {
      type: Number as PropType<number>,
      required: true,
    },
    title: {
      type: String as PropType<string>,
      required: true,
    },
  },
  computed: {
    ...mapState("auth", ["user"]),
  },
  methods: {
    async reserve() {
      try {
        this.isLoading = true;
        let apId = "";
        if (typeof this.$route.params.id === "string") {
          apId = this.$route.params.id;
        }
        const userId = this.user.uid;
        if (!this.isReserved) {
          await API.reserveApartment(apId, userId);
          this.isReserved = true;
        } else {
          await API.unReserveApartment(apId, userId);
          this.isReserved = false;
        }
      } catch (error) {
        console.log("error: ", error);
      } finally {
        this.isLoading = false;
      }
    },
  },
});
