
import { defineComponent } from "vue";
import StarRating from "../reusable/StarRating.vue";

export default defineComponent({
  name: "ApartmentFeedbackItem",
  components: {
    StarRating,
  },
  props: {
    rating: {
      type: Number,
      required: true,
    },
    name: {
      type: String,
      default: "No name",
    },
    text: {
      type: String,
      default: "",
    },
  },
});
